.jpum-user-codes {
  display: none;
  position: absolute;
  right: 1px;
  bottom: 1px;
}

.jpum-user-codes > span {
  border: 1px solid;
  border-radius: 2px;
  background-color: #23282d;
  color: #eee;
  text-align: center;
  cursor: pointer;
  font-size: 20px;
  height: 20px;
  width: 20px;
}

.jpum-user-codes:hover > span {
  background-color: #0073aa;
  color: #fff;
}

.jpum-user-codes ul {
  display: none;
  position: absolute;
  top: 1px;
  left: 20px;
  background-color: #fff;
  width: auto;
  z-index: 999;
  /*box-shadow: 1px 1px 5px -1px; */
}

.jpum-user-codes ul li {
  display: block;
  padding: .5em;
  border-bottom: 1px dashed rgba(0, 0, 0, 0.25);
  background-color: #23282d;
  color: #eee;
  cursor: pointer;
  line-height: 1;
  white-space: nowrap;
}

.jpum-user-codes ul li:last-child {
  border-bottom: 0;
}

.jpum-user-codes ul li:hover {
  background-color: #0073aa;
}

.jpum-user-codes ul li:hover > span {
  color: #fff;
}

.jpum-user-codes.open ul {
  display: block;
}

#menu-to-edit .menu-item p.description-wide {
  position: relative;
}

#menu-to-edit .menu-item .nav_item_options-roles label {
  width: 33%;
  display: inline-block;
  float: left;
  margin-top: 5px;
}

#menu-to-edit .menu-item.show-insert-button .jpum-user-codes {
  display: block;
}

